<script>
  export default {
    data () {
      return {
        search: '',
        sortBy: '',
        keys: [
          'All',
          'Active',
          'Sespended',
          'Secession',
        ],
        headers: [
          { text: 'Code', align: 'start', value: 'code', },
          { text: 'Name', value: 'name' },
          { text: 'Email', value: 'email' },
          { text: 'Phone', value: 'phone' },
          { text: 'Country', value: 'country' },
          { text: 'Member level', value: 'level' },
          { text: 'Credit', value: 'credit' },
          { text: 'Join date', value: 'join' },
          { text: 'Secession', value: 'secession' },
          { text: 'Select', value: 'checkbox' },
          { text: 'Info', value: 'actions', sortable: false },
        ],
        dialog: false,
        firstName: '',
        secondName: '',
        displayName: '',
        phoneNumber: '',
        emailAdress: '',
        joinDate: '',
        secessionDate: '',
        imgUrl: '',
        countryName: '',
        adressOne: '',
        adressTwo: '',
        postalCode: '',
        cityName: '',
        stateName: '',
        companyName: '',
      }
    },

    methods: {
      showDialog (item) {
        this.dialog = true

        this.firstName = item.name
        this.secondName = item.secondName
        this.displayName = item.displayName
        this.phoneNumber = item.phone
        this.emailAdress = item.email
        this.joinDate = item.join
        this.secessionDate = item.secession
        this.imgUrl = item.img
        this.countryName = item.country
        this.adressOne = item.adressOne
        this.adressTwo = item.adressTwo
        this.postalCode = item.postalCode
        this.cityName = item.city
        this.stateName = item.state
        this.companyName = item.company
      },
      closeDialog () {
        this.dialog = false
      },
      resetInput() {
        let inputCheckbox = document.querySelectorAll('.table_head .v-input--selection-controls__input input')

        inputCheckbox.forEach(element => {
          if (element.checked) {
            element.click()
          }
        })

        this.search = ''
        this.sortBy = ''
      },
    },
  }
</script>
