import Vue from 'vue'
import VueRouter from 'vue-router'
import Inquiry from '../views/Inquiry.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/inquiry',
    name: 'inquiry',
    component: Inquiry
  },
  {
    path: '*',
    name: 'any',
    component: Inquiry
  },
  {
    path: '/member',
    name: 'member',
    component: () => import('../views/Member.vue')
  },
  {
    path: '/editor',
    name: 'editor',
    component: () => import('../views/Editor.vue')
  },
  {
    path: '/credit',
    name: 'credit',
    component: () => import('../views/Credit.vue')
  },
  {
    path: '/purchase',
    name: 'purchase',
    component: () => import('../views/Purchase.vue')
  },
  {
    path: '/credit/history',
    name: 'credit/history',
    component: () => import('../views/CreditHistory.vue')
  },
  {
    path: '/refund',
    name: 'refund',
    component: () => import('../views/Refund.vue')
  },
  {
    path: '/gift',
    name: 'gift',
    component: () => import('../views/Gift.vue')
  },
  {
    path: '/merchant',
    name: 'merchant',
    component: () => import('../views/Merchant.vue')
  },
  {
    path: '/terms',
    name: 'terms',
    component: () => import('../views/Terms.vue')
  },
  {
    path: '/withdrawal',
    name: 'withdrawal',
    component: () => import('../views/Withdrawal.vue')
  },
  {
    path: '/withdrawal/completed',
    name: 'withdrawal/completed',
    component: () => import('../views/Completed.vue')
  },
  {
    path: '/pattern/approval',
    name: 'pattern/approval',
    component: () => import('../views/Approval.vue')
  },
  {
    path: '/pattern',
    name: 'pattern',
    component: () => import('../views/Pattern.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
